*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'PT Sans', sans-serif;
}

.clear{
    clear: both;
}

.navbar{
    display: flex;
    background: #FF9000;
    height: 90px;
    color: white;
}

.logo{
    width: 50%;
    float: left;
    padding: 30px 30px;
    font-size: 20px;
    /* background: #fff; */
}

.desktop{
    width: 50%;
    float: right;
}

.desktop ul{
    float: right;
}

.desktop ul li{
    float: left;
    margin-top: 20px;
    margin-left: 10px;
    list-style-type: none;
    padding: 10px 10px;
    font-size: 20px;
    transition: .5s;
    cursor: pointer;
    border-radius: 30px;
    background: #FF9000;
}

.desktop ul li:hover, 
.desktop ul li.ativo{
    background: white;
    color: #FF9000;
}

.sobre h2{
    font-weight: bold;
    text-align: center;
    padding: 50px 2%;
    color: #FF9000;
    font-size: 50px;
    
}

.sobre p{
    text-align: justify;
    margin: 30px;
    font-size: 25px;
}

.membros{
    padding: 50px;
}

.cabeceira{
    background-image: url('../../images/Cabeceira.jpg');
}

.limirio{
    background-image: url('../../images/limirio.jpg');
}

.breno{
    background-image: url('../../images/bob.jpg');
}

.bruno{
    background-image: url('../../images/bruno.jpeg');
}

.raquel{
    background-image: url('../../images/raquel.jpeg');
}

.henrique{
    background-image: url('../../images/henrique.jpeg');
}

.felipe{
    background-image: url('../../images/felipe.jpeg');
}

.membros ul {
    text-align: center;
}

.membros ul a li{
    display: inline-block;
    background-size: cover;
    width: 40px;
    height: 40px;
    border-radius: 40px;
    margin: 20px;
    list-style-type: none;
    cursor: pointer;
}









